import React, { useState } from 'react';
import './Shape.css';


const Shape = () => {
    const [BoxHeight_simple , setBoxHeight_simple ] = useState(10);
    const [BoxWidth_simple , setBoxWidth_simple ] = useState(10);
    const [Radius_top_left_simple , setRadius_top_left_simple ] = useState(0);
    const [Radius_top_right_simple , setRadius_top_right_simple ] = useState(0);
    const [Radius_bottom_left_simple , setRadius_bottom_left_simple ] = useState(0);
    const [Radius_bottom_right_simple , setRadius_bottom_right_simple ] = useState(0);
    const [Box_Simple_Rotate_By , setBox_Simple_Rotate_By ] = useState(0);


    const [BoxHeight, setBoxHeight] = useState(10);
    const [BoxWidth, setBoxWidth] = useState(10);
    const [HZ_Radius_top_left, setHZ_Radius_top_left] = useState(0);
    const [VR_Radius_top_left, setVR_Radius_top_left] = useState(0);
    const [HZ_Radius_top_right, setHZ_Radius_top_right] = useState(0);
    const [VR_Radius_top_right, setVR_Radius_top_right] = useState(0);
    const [HZ_Radius_bottom_left, setHZ_Radius_bottom_left] = useState(0);
    const [VR_Radius_bottom_left, setVR_Radius_bottom_left] = useState(0);
    const [HZ_Radius_bottom_right, setHZ_Radius_bottom_right] = useState(0);
    const [VR_Radius_bottom_right, setVR_Radius_bottom_right] = useState(0);
    const [Box_Detailed_Rotate_By, setBox_Detailed_Rotate_By] = useState(0);


    const [Diameter_X, setDiameter_X] = useState(10);
    const [Diameter_Y, setDiameter_Y] = useState(10);
    const [Circle_Rotate_By, setCircle_Rotate_By] = useState(0);


    const [Height, setHeight] = useState(5);
    const [Width, setWidth] = useState(5);
    const [BorderTop, setBorderTop] = useState(5);
    const [BorderRight, setBorderRight] = useState(5);
    const [BorderLeft, setBorderLeft] = useState(5);
    const [BorderBottom, setBorderBottom] = useState(5);
    const [Triangle_Rotate_By, setTriangle_Rotate_By] = useState(0);

    return (
        <div className="shape-page-container">

            {/* CIRCLE */}
            <div className="shape-container" style={{display : 'flex', justifyContent : 'center', alignItems : 'center', flexWrap : 'wrap', color : 'white'}}>

                <div className="info" title="If you want to make CIRCLE then keep height and width same. For elipse you need to set height different that width.">i</div>

                {/* Actual Shape */}
                <div className="shape shape-circle" style={{'--Diameter_Y' : `${Diameter_Y}rem`, '--Diameter_X' : `${Diameter_X}rem`, '--Circle_Rotate_By' : `${Circle_Rotate_By}deg` }}></div>
                
    
                {/* Controls */}
                <div className="triangle-controls">
                    height(rem) : <input type="number" value={Diameter_Y} onChange={(e)=>{setDiameter_Y(e.target.value)}}/>
                    <br/>
                    width(rem) : <input type="number" value={Diameter_X} onChange={(e)=>{setDiameter_X(e.target.value)}}/>
                    <br/>
                    rotate(deg) : <input type="number" value={Circle_Rotate_By} onChange={(e)=>{setCircle_Rotate_By(e.target.value)}}/>
                </div>

                <pre className="final-css">
                    {`
                        .circle{
                            height: ${Diameter_Y}rem;
                            width: ${Diameter_X}rem;
                            background-color: blueviolet;
                            border-radius: 50%;
                            transform: rotate(${Circle_Rotate_By});
                        }
                    `}
                </pre>
            </div>


            <hr/>


            {/* SQUARE, RECTANGLE, TRIANGLE And TRAPAZOID */}
            <div className="shape-container" style={{display : 'flex', justifyContent : 'center', alignItems : 'center', flexWrap : 'wrap', color : 'white'}}>

                <div className="info" title="To make a triangle you need to make height and width to 0px and give TOP border as 'top-border : 3rem solid red' And make LEFT and RIGHT border of same height(3rem) but keep them TRANSPARENT. Finaly make opposite (BOTTOM) border of 0rem">i</div>

                {/* Actual Shape */}
                <div className="shape shape-triangle" style={{'--Height' : `${Height}rem`, '--Width' : `${Width}rem`, '--BorderTop' : `${BorderTop}rem`, '--BorderRight' : `${BorderRight}rem`, '--BorderLeft' : `${BorderLeft}rem`, '--BorderBottom' : `${BorderBottom}rem`, '--Triangle_Rotate_By' : `${Triangle_Rotate_By}deg`}}>
                    {/* No content */}
                </div>

                {/* Controls */}
                <div className="triangle-controls">
                    height(rem) : <input type="number" value={Height} onChange={(e)=>{setHeight(e.target.value)}}/>
                    <br/>
                    width(rem) : <input type="number" value={Width} onChange={(e)=>{setWidth(e.target.value)}}/>
                    <br/>
                    border-top(rem) : <input type="number" value={BorderTop} onChange={(e)=>{setBorderTop(e.target.value)}}/>
                    <br/>
                    border-right(rem) : <input type="number" value={BorderRight} onChange={(e)=>{setBorderRight(e.target.value)}}/>
                    <br/>
                    border-left(rem) : <input type="number" value={BorderLeft} onChange={(e)=>{setBorderLeft(e.target.value)}}/>
                    <br/>
                    border-bottom(rem) : <input type="number" value={BorderBottom} onChange={(e)=>{setBorderBottom(e.target.value)}}/>
                    <br/>
                    rotate(deg) : <input type="number" value={Triangle_Rotate_By} onChange={(e)=>{setTriangle_Rotate_By(e.target.value)}}/>
                </div>

                <pre className="final-css">
                    {`
                        .different-other-shapes{
                            height: ${Height}rem;
                            width: ${Width}rem;
                            background-color: sienna;
                            border-top: ${BorderTop}rem solid blueviolet;
                            border-right: ${BorderRight}rem solid tomato;
                            border-left: ${BorderLeft}rem solid yellow;
                            border-bottom: ${BorderBottom}rem solid springgreen;
                            transform: rotate(${Triangle_Rotate_By});
                        }
                    `}
                </pre>

            </div>


            <hr/>

            {/* Square Rectandle Border Shapes (SIMPLE) */}
            <div className="shape-container" style={{display : 'flex', justifyContent : 'center', alignItems : 'center', flexWrap : 'wrap', color : 'white'}}>
            
                <div className="info" title="Adjust the radius of all 4 corners to get the different shapes.">i</div>
                {/* Actual Shape */}
                <div className="shape shape-curvey-box-simple" style={
                    {
                        '--BoxHeight_simple' : `${BoxHeight_simple}rem`,
                        '--BoxWidth_simple' : `${BoxWidth_simple}rem`,
                        '--Radius_top_left_simple' : `${Radius_top_left_simple}rem`,
                        '--Radius_top_right_simple' : `${Radius_top_right_simple}rem`,
                        '--Radius_bottom_left_simple' : `${Radius_bottom_left_simple}rem`,
                        '--Radius_bottom_right_simple' : `${Radius_bottom_right_simple}rem`,
                        '--Box_Simple_Rotate_By' : `${Box_Simple_Rotate_By}deg`
                    }
                }>
                    {/* No Content */}
                </div>

                {/* Controls */}
                <div className="triangle-controls">
                        height(rem) : <input type="number" value={BoxHeight_simple} onChange={(e)=>{setBoxHeight_simple(e.target.value)}}/>
                        <br/>
                        width(rem) : <input type="number" value={BoxWidth_simple} onChange={(e)=>{setBoxWidth_simple(e.target.value)}}/>
                        <br/>
                        Radius_top_left(rem) : <input type="number" value={Radius_top_left_simple} onChange={(e)=>{setRadius_top_left_simple(e.target.value)}}/>
                        <br/>
                        Radius_top_right(rem) : <input type="number" value={Radius_top_right_simple} onChange={(e)=>{setRadius_top_right_simple(e.target.value)}}/>
                        <br/>
                        Radius_bottom_left(rem) : <input type="number" value={Radius_bottom_left_simple} onChange={(e)=>{setRadius_bottom_left_simple(e.target.value)}}/>
                        <br/>
                        Radius_bottom_right(rem) : <input type="number" value={Radius_bottom_right_simple} onChange={(e)=>{setRadius_bottom_right_simple(e.target.value)}}/>
                        <br/>
                        rotate(deg) : <input type="number" value={Box_Simple_Rotate_By} onChange={(e)=>{setBox_Simple_Rotate_By(e.target.value)}}/>
                </div>

                <pre className="final-css">
                    {`
                        .curved-box{
                            height: ${BoxHeight_simple}rem;
                            width: ${BoxWidth_simple}rem;
                            background-color: deepskyblue;
                            border: 1px solid white;
                            border-top-left-radius: ${Radius_top_left_simple}rem;
                            border-top-right-radius: ${Radius_top_right_simple}rem;
                            border-bottom-left-radius: ${Radius_bottom_left_simple}rem;
                            border-bottom-right-radius: ${Radius_bottom_right_simple}rem;
                            transform: rotate(${Box_Simple_Rotate_By});
                        }
                    `}
                </pre>
            </div>



            <hr/>

            {/* Square Rectandle Border Shapes (DETAILED) */}
            <div className="shape-container" style={{display : 'flex', justifyContent : 'center', alignItems : 'center', flexWrap : 'wrap', color : 'white'}}>

                <div className="info" title="'border-top-left-radius' The property takes in one or two values that define the radii of a quarter ellipse that defines the shape of the corner of the outer border edge. The first value is the horizontal radius, the second the vertical radius. If the second value is omitted it is copied from the first. If either length is zero, the corner is square, not rounded. Percentages for the horizontal radius refer to the width of the border box, whereas percentages for the vertical radius refer to the height of the border box. Negative values are not allowed.">i</div>

                {/* Actual Shape */}
                <div className="shape shape-curvey-box-detailed" style={
                    {
                        '--BoxHeight' : `${BoxHeight}rem`,
                        '--BoxWidth' : `${BoxWidth}rem`,
                        '--HZ_Radius_top_left' : `${HZ_Radius_top_left}rem`,
                        '--VR_Radius_top_left' : `${VR_Radius_top_left}rem`,
                        '--HZ_Radius_top_right' : `${HZ_Radius_top_right}rem`,
                        '--VR_Radius_top_right' : `${VR_Radius_top_right}rem`,
                        '--HZ_Radius_bottom_right' : `${HZ_Radius_bottom_right}rem`,
                        '--VR_Radius_bottom_right' : `${VR_Radius_bottom_right}rem`,
                        '--HZ_Radius_bottom_left' : `${HZ_Radius_bottom_left}rem`,
                        '--VR_Radius_bottom_left' : `${VR_Radius_bottom_left}rem`,
                        '--Box_Detailed_Rotate_By' : `${Box_Detailed_Rotate_By}deg`
                    }
                }>
                    {/* No Content */}
                </div>


                {/* Controls */}
                <div className="triangle-controls">
                        height(rem) : <input type="number" value={BoxHeight} onChange={(e)=>{setBoxHeight(e.target.value)}}/>
                        <br/>
                        width(rem) : <input type="number" value={BoxWidth} onChange={(e)=>{setBoxWidth(e.target.value)}}/>
                        <br/>
                        HZ_Radius_top_left(rem) : <input type="number" value={HZ_Radius_top_left} onChange={(e)=>{setHZ_Radius_top_left(e.target.value)}}/>
                        VR_Radius_top_left(rem) : <input type="number" value={VR_Radius_top_left} onChange={(e)=>{setVR_Radius_top_left(e.target.value)}}/>
                        <br/>
                        HZ_Radius_top_right(rem) : <input type="number" value={HZ_Radius_top_right} onChange={(e)=>{setHZ_Radius_top_right(e.target.value)}}/>
                        VR_Radius_top_right(rem) : <input type="number" value={VR_Radius_top_right} onChange={(e)=>{setVR_Radius_top_right(e.target.value)}}/>
                        <br/>
                        HZ_Radius_bottom_right(rem) : <input type="number" value={HZ_Radius_bottom_right} onChange={(e)=>{setHZ_Radius_bottom_right(e.target.value)}}/>
                        VR_Radius_bottom_right(rem) : <input type="number" value={VR_Radius_bottom_right} onChange={(e)=>{setVR_Radius_bottom_right(e.target.value)}}/>
                        <br/>
                        HZ_Radius_bottom_left(rem) : <input type="number" value={HZ_Radius_bottom_left} onChange={(e)=>{setHZ_Radius_bottom_left(e.target.value)}}/>
                        VR_Radius_bottom_left(rem) : <input type="number" value={VR_Radius_bottom_left} onChange={(e)=>{setVR_Radius_bottom_left(e.target.value)}}/>
                        <br/>
                        rotate(deg) : <input type="number" value={Box_Detailed_Rotate_By} onChange={(e)=>{setBox_Detailed_Rotate_By(e.target.value)}}/>
                </div>

                <pre className="final-css">
                    {`
                        .curved-box{
                            height: ${BoxHeight}rem;
                            width: ${BoxWidth}rem;
                            background-color: transparent;
                            border: 5px solid white;
                            border-top-left-radius: ${HZ_Radius_top_left}rem ${VR_Radius_top_left}rem;
                            border-top-right-radius: ${HZ_Radius_top_right}rem ${VR_Radius_top_right}rem;
                            border-bottom-left-radius: ${HZ_Radius_bottom_left}rem ${VR_Radius_bottom_left}rem;
                            border-bottom-right-radius: ${HZ_Radius_bottom_right}rem ${VR_Radius_bottom_right}rem;
                            transform: rotate(${Box_Detailed_Rotate_By});
                        }
                    `}
                </pre>
            </div>



        </div>
    )
}

export default Shape;





// title="Give same height and width to DIV and make border-radius to 50%."
// title="Make height and width to 0px and give TOP border as 'top-border : 3rem solid red' And make LEFT and RIGHT border of same height(3rem) but keep them TRANSPARENT. Finaly make opposite (BOTTOM) border of 0rem"
// title="Make height and width to 0px and give RIGHT border as 'right-border : 3rem solid red' And make TOP and BOTTOM border of same height(3rem) but keep them TRANSPARENT. Finaly make opposite (LEFT) border of 0rem"
// title="Make height and width to 0px and give BOTTOM border as 'bottom-border : 3rem solid red' And make LEFT and RIGHT border of same height(3rem) but keep them TRANSPARENT. Finaly make opposite (TOP) border of 0rem"
// title="Make height and width to 0px and give LEFT border as 'left-border : 3rem solid red' And make TOP and BOTTOM border of same height(3rem) but keep them TRANSPARENT. Finaly make opposite (RIGHT) border of 0rem"
// title="Keep all the borders same as right pointing triangle but for this just increase the width of LEFT border."